<!--
  TO-DO: merge ActorQuery.vue & ActorMove.vue into a single component, 
         so querying and actions happen on same page…
-->

<template>
  <v-card>
    <v-card-title>
      <div class="headline font-weight-medium blue--text">
        <v-icon large>pageview</v-icon>
          {{ $t('Query a Unit') }}
      </div>
    </v-card-title>
    <v-card-text>
      <v-form>
        <!-- static contract address & ABI replaces this from - aperez 20Sep2021 -->
        <!--
        <v-text-field
          label="Contract Address"
          v-model.trim="contractAddress"
          required
          outline
          background-color="blue lighten-1"
        ></v-text-field>
        <v-textarea
          label="ABI"
          v-model.trim="abi"
          required
          outline
          background-color="blue lighten-1"
          @input="decodeAbi"
        ></v-textarea>
        -->
        
        <!-- old version - aperez 20Sep2021 -->
        <!--
        <v-select
          v-if="parsedAbi"
          :items="parsedAbi"
          label="Method"
          v-model="method"
          single-line
          bottom
          outline
          background-color="blue lighten-1"
        ></v-select>
        -->

        <!-- version siempre visible - aperez 20Sep2021 -->
        <v-select

          :items="parsedAbi"
          label="Method"
          v-model="method"
          single-line
          bottom
          outline
          background-color="blue lighten-1"
        ></v-select>

        <template v-if="params">
          <v-text-field
            v-for="(param, index) in params"
            :label="param.name"
            :key="index"
            v-model="inputParams[index]"
            outline
            background-color="blue lighten-1"
          ></v-text-field>
        </template>
      </v-form>
    </v-card-text>
    
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn class="success" dark @click="callTo" :disabled="notValid">{{ $t('common.confirm') }}</v-btn>
      <v-spacer></v-spacer>
    </v-card-actions>
    
    <v-dialog v-model="execResultDialog" persistent max-width="50%">
      <v-card>
        <v-card-title>
          <span class="headline">
            {{ $t('call_contract.result') }}
          </span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12>
                <v-textarea label="Result" v-model="result" disabled></v-textarea>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="blue--text darken-1" flat @click.native="execResultDialog = false">{{ $t('common.confirm') }}</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    
  </v-card>
</template>

<script>
import webWallet from 'libs/web-wallet'
import abi from 'ethjs-abi'
import server from 'libs/server'

// aperez 27Sep2021
// pending hacer funcion general que recibe un HEX y los decodifica y prueba si es
// un número, si no lo es repite la operacion usando hex_to_ascii(...)
function hex_to_ascii(hexString) { // aperez 27Sep2021
  var hex  = hexString.toString();
	var str = '';
	for (var n = 0; n < hex.length; n += 2) {
		str += String.fromCharCode(parseInt(hex.substr(n, 2), 16));
	}
  // pendiente trim/suprimir simbolos no ascii y espacios
	return str;
}


// aperez 07Nov2022
const contractAddress = "f24f7456aa7ee076db4339a00d5c523a3360b30e"; // test Actor Dapp contract
const abiJson = JSON.parse('[{	"constant": true,	"inputs": [{"name": "unit_Id","type": "uint32"}],"name": "getLocationFor","outputs": [{"name": "tile_location_hex_Id","type": "string"}],"payable": false,"stateMutability": "view","type": "function"},{"constant": true,"inputs": [{"name": "unit_Id","type": "uint32"}],"name": "get_nameby","outputs": [{"name": "","type": "string"}],"payable": false,"stateMutability": "view","type": "function"}]');
// the above is a "call contract" ABI

// aperez 27Sep2021 - ABI parsing
const parsedAbi = [];
for (let i = 0; i < abiJson.length; i++) {
          parsedAbi[i] = {text: abiJson[i]['name'], value: i, info: abiJson[i]}
} // en of aperez code segment 27Sep2021

export default {
  data () {
    return {
      contractAddress, // modified from inlined default to passing param from above - aperez 27Sep2021
      abi, // modified from inlined default to passing param from above - aperez 27Sep2021
      parsedAbi, // modified from inlined default to passing param from above - aperez 27Sep2021
      method: null,
      inputParams: [],
      execResultDialog: false,
      result: 'loading...',
    }
  },
  computed: {
    params: function() {
      if (this.method === null) {
        return null
      }
      const inputs = this.parsedAbi[this.method].info.inputs
      if (inputs.length > 0) {
        return inputs
      }
      return null
    },
    notValid: function() {
      //@todo valid the address
      return !(this.method !== null)
    }
  },
  watch: {
    method: function() {
      this.inputParams = []
    }
  },
  methods: {
    decodeAbi() {  // this function is ommited ... not called by commented @input="decodeAbi" in page form - aperez 08Nov2022
      try {
        const abiJson = JSON.parse(this.abi)
        this.parsedAbi = []
        for (let i = 0; i < abiJson.length; i++) {
          this.parsedAbi[i] = {text: abiJson[i]['name'], value: i, info: abiJson[i]}
        }
      } catch (e) {
        this.$root.log.error('call_contract_decode_abi_error', e.stack || e.toString() || e)
        return true
      }
    },
    async callTo() {
      try {
        const encodedData = abi.encodeMethod(this.parsedAbi[this.method].info, this.inputParams).substr(2)
        this.execResultDialog = true
        try {
          // aperez 27Sep2021
          const hexResult = await webWallet.getWallet().callContract(this.contractAddress, encodedData);
          this.result = hex_to_ascii(hexResult); // aperez 27Sep2021
        } catch (e) {
          this.$root.log.error('call_contract_call_contract_error', e.stack || e.toString() || e)
          alert(e.message || e)
          this.execResultDialog = false
        }
      } catch (e) {
        this.$root.error('Params error')
        this.$root.log.error('call_contract_encode_abi_error', e.stack || e.toString() || e)
        return false
      }
    }
  }
}
</script>



