      this.inputParams_send[1] = this.move_to_quadrant; // overwrites FORM data
      console.log("'ACTOR_ID' followed by 'Quadrant_ID'\n"+JSON.stringify(this.inputParams_send)); // visible while in DEV, to be commented
      //alert("Ready to send encoded data:\n"+JSON.stringify(abi.encodeMethod(this.parsedAbi_send[this.method_send].info, this.inputParams_send).substr(2))); // visible while in DEV, to be commented
      if(this.move_to_quadrant==0){ // attempt to move to sea tiles or not selected tile not allowed - aperez 23Nov2022
        alert("Please select a valid destination");
      }else{
        this.send(); // calls the default sendToContract method - 23Nov2022
        // possibly here the TRIGGER of TO-DOs #5 & #6 & #7
      } 
    },
    set_moveToQuadrant: function (quadrantId){ // replaces quadrant TEXT_FORM for button UI data input - aperez 14Nov2022
      this.move_to_quadrant = quadrantId;
      //alert("destination set for #"+this.move_to_quadrant+" location");      
    },
    call_locateActor: function (){
      this.parsedAbi_call = parseAbis(abiJson_actorlocation_call,'name'); // locally parsing specific ABIJson to global parsedABIs - aperez 24Nov2022
      this.method_call = 0;
      this.inputParams_call[0] = this.actorId;
      this.callTo();
    },
    set_selectActor: function (sel_actorId){
      this.actorId = sel_actorId;
    },

    
    /* workaround functions --get_TXstatus() &  fetchAsync()-- to capture the status of a TX to give 
       the user a in-game-response - aperez 24Nov2022
       a two function solution from https://gist.github.com/msmfsd/fca50ab095b795eb39739e8c4357a808
       and from https://stackoverflow.com/questions/247483/http-get-request-in-javascript/4033310#4033310
    */
    // TO-DO #7: trigger this method every x-time using examples as:
    //               https://stackoverflow.com/questions/13304471/javascript-get-code-to-run-every-minute
    //            or https://stackoverflow.com/questions/3138756/calling-a-function-every-60-seconds
    get_TXstatus: function () {
      // trigger async function and then log response or catch error of fetch promise
      console.log("Ready to fetch");
      this.fetchAsync()
        .then(data => console.log(data))
        .catch(reason => console.log(reason.message));
      // if success status is reached ... change the "flag_checkTXstatus" back to false
    },
    
    async fetchAsync() {
      // await response of fetch call
      let response = await fetch('https://api.github.com'); // use URL like https://explorer.htmlcoin.com/tx/6dab1f73916af120f0f15947a7e66928386f078200e5500173167a1f2983ec05
      // the URL should be the recently generated transaction to follow on status "unconfirmed or success or failed"
      // only proceed once promise is resolved
      let data = await response.json();
      // only proceed once second promise is resolved
      return data;

      /* TO-DO #8: fetch the URL TX-html parse it to read list__right for "TXReceiptStatus" in HTML-block - aperez 24Nov2022
      <li class="list__item ng-scope" ng-if="TC.tx.receipt &amp;&amp; TC.tx.receipt[0]">
	      <div class="list__left">
		      <b translate="">
		        <span class="ng-scope">TxReceiptStatus</span></b>
	      </div>
	      <div class="list__right">
		      <div class="ng-binding">Success</div>
	      </div>
      </li>
    */

    },


    // ********** THIRD PARTY VUE-METHODS IN SECTION BELLOW - aperez 14Nov2022 ********** 
    async send() {
      try {
	      const encodedData = abi.encodeMethod(this.parsedAbi_send[this.method_send].info, this.inputParams_send).substr(2)
        this.confirmSendDialog = true
        try {
          this.rawTx = await webWallet.getWallet().generateSendToContractTx(this.contractAddress, encodedData, this.gasLimit, this.gasPrice, this.fee, this.amount)
        } catch (e) {
          this.$root.log.error('send_to_generate_tx_error', e.stack || e.toString() || e)
          alert(e.message || e)
          this.confirmSendDialog = false
          return false
        }
        this.canSend = true
      } catch (e) {
        this.$root.error('Params error')
	      alert(e.message || e)
        this.$root.log.error('send_to_contract_encode_abi_error', e.stack || e.toString() || e)
        this.confirmSendDialog = false
        return false
      }
    },
    async confirmSend() {
      this.sending = true
      try {
        const txId = await webWallet.getWallet().sendRawTx(this.rawTx)
        this.confirmSendDialog = false
        this.sending = false
        const txViewUrl = server.currentNode().getTxExplorerUrl(txId);
        this.$root.success(`Successful sent! You can follow the transaction on <a href="${txViewUrl}" target="_blank">${txViewUrl}</a>`, true, 0);
        // above line to be replaced by a U.I. element that reacts every x-time to updates from above function "get_TXstatus()" - aperez 24Nov2022
        // initiates a watch on TX URL by activating "flag_checkTXstatus" as true
        this.$emit('send')
      } catch (e) {
        alert(e.message || e)
        this.$root.log.error('send_to_contract_post_raw_tx_error', e.response || e.stack || e.toString() || e)
        this.confirmSendDialog = false
      }
    }, 

    async callTo() {
      try {
        const encodedData = abi.encodeMethod(this.parsedAbi_call[this.method_call].info, this.inputParams_call).substr(2)
        this.execResultDialog = true
        try {
          const hexResult = await webWallet.getWallet().callContract(this.contractAddress, encodedData); // aperez 27Sep2021
          { // this block of code manages the result cases STRING & NON-STRING - aperez 20Nov2022
            var parsedOutputs = [];
            parsedOutputs = parseAbis(this.parsedAbi_call[this.method_call].info.outputs,'type');
            // console.log(parsedOutputs[0].text);
            if(parsedOutputs[0].text == 'string'){
              this.result = hex_to_ascii(hexResult);
            }else{
              this.result = hexResult; // TO-DO #9: develop managing functions for UINTs (unsigned Ints) as encoded in ABIs
            }
          } 
          // line bellow replaced by above block - aperez 20Nov2022
          // this.result = hex_to_ascii(hexResult); // aperez 27Sep2021
        } catch (e) {
          this.$root.log.error('call_contract_call_contract_error', e.stack || e.toString() || e)
          alert(e.message || e)
          this.execResultDialog = false
        }
      } catch (e) {
        this.$root.error('Params error')
        this.$root.log.error('call_contract_encode_abi_error', e.stack || e.toString() || e)
        return false
      }
    }
  }
}
</script>



