<!--
  TO-DO: merge ActorQuery.vue & ActorMove.vue into a single component, 
         so querying and actions happen on same page…
-->

<template>
  <v-card>
    <v-card-title>
      <div class="headline font-weight-medium blue--text">
        <v-icon large>play_circle_outline</v-icon>
          {{ $t('Move Actor') }}
      </div>
    </v-card-title>
    <v-card-text>
      <v-form>
        <!-- static contract address & ABI replaces this form - aperez 08Nov2022 -->
        <!--
        <v-text-field
          label="Contract Address"
          v-model.trim="contractAddress"
          required
          outline
          background-color="blue lighten-1"
        ></v-text-field>
        <v-textarea
          label="ABI"
          v-model.trim="abi"
          required
          outline
          background-color="blue lighten-1"
          @input="decodeAbi"
        ></v-textarea>
        -->

        <!-- old version - aperez 08Nov2022 -->   
        <!--
        <v-select
          v-if="parsedAbi"
          :items="parsedAbi"
          label="Method"
          v-model="method"
          @change="amount=0"
          outline
          background-color="blue lighten-1"
          single-line
          bottom
        ></v-select>
        -->
    

        <!-- version siempre visible  - aperez 08Nov2022 -->
        <v-select

          :items="parsedAbi"
          label="Method"
          v-model="method"
          @change="amount=0"
          outline
          background-color="blue lighten-1"
          single-line
          bottom
        ></v-select>

        <template v-if="params">
          <v-text-field
            v-for="(param, index) in params"
            :label="param.name"
            :key="index"
            v-model="inputParams[index]"
            outline
            background-color="blue lighten-1"
          ></v-text-field>
        </template>
        <template v-if="payable">
          <v-text-field
            label="amount"
            v-model="amount"
            outline
            background-color="blue lighten-1"
          ></v-text-field>
        </template>
        <v-text-field
          label="Gas Price (1e-8 HTML/gas)"
          v-model.trim="gasPrice"
          outline
          background-color="indigo darken-4"
          required
        ></v-text-field>
        <v-text-field
          label="Gas Limit"
          v-model.trim="gasLimit"
          outline
          background-color="indigo darken-4"
          required
        ></v-text-field>
        <v-text-field
          label="Fee"
          v-model.trim="fee"
          outline
          background-color="indigo darken-4"
          required
          ></v-text-field>
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn class="success" dark @click="send" :disabled="notValid">{{ $t('common.confirm') }}</v-btn>
      <v-spacer></v-spacer>
    </v-card-actions>
    <v-dialog v-model="confirmSendDialog" persistent max-width="50%">
      <v-card>
        <v-card-title>
          <span class="headline">
            {{ $t('send_to_contract.confirm') }}
          </span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12>
                <!-- "fixed" textfield to textarea aperez 10-nov-2022 -->
                <!--<v-textarea label="Raw Tx" v-model="rawTx" disabled></v-text-field>-->
                <v-textarea label="Raw Tx" v-model="rawTx" disabled></v-textarea>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="blue--text darken-1" flat @click="confirmSend" v-show="canSend && !sending">{{ $t('common.confirm') }}</v-btn>
          <v-btn class="red--text darken-1" flat @click.native="confirmSendDialog = false" :v-show="!sending">{{ $t('common.cancel') }}</v-btn>
          <v-progress-circular indeterminate :size="20" v-show="sending" class="primary--text"></v-progress-circular>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </v-card>
</template>


<script>
import webWallet from 'libs/web-wallet'
import abi from 'ethjs-abi'
import server from 'libs/server'

// aperez 08Nov2022 TO-DO: finish return
function walletAddr_dappEncode(b){
  const bytes = bs58.decode(addr);
  hexAddr = bytes.toString('hex');
  hexAddr = hexAddr.slice(2, 42); // Removes first byte (version) & last 4 bytes (checksum)
  // pendiente return, return luego de agregar prefijo '0x'
}

// aperez 08Nov2022
const contractAddress = "f24f7456aa7ee076db4339a00d5c523a3360b30e"; // test Actor Dapp contract
const abiJson = JSON.parse('[{"constant": false,"inputs": [{"name": "unit_Id","type": "uint32"},{"name": "_quadrant","type": "uint256"}],"name": "changeLocation_mod","outputs": [],"payable": false,"stateMutability": "nonpayable","type": "function"}]');
// the above is a "send to contract" ABI

// aperez 08Nov2022 - ABI parsing
const parsedAbi = [];
for (let i = 0; i < abiJson.length; i++) {
          parsedAbi[i] = {text: abiJson[i]['name'], value: i, info: abiJson[i]}
} // en of aperez code segment 08Nov2022

export default {
  data () {
    return {
      contractAddress, // modified from inlined default to passing param from above - aperez 08Nov2022
      abi, // modified from inlined default to passing param from above - aperez 08Nov2022
      parsedAbi, // modified from inlined default to passing param from above - aperez 08Nov2022
      method: null,
      inputParams: [],
      gasPrice: '40',
      gasLimit: '2500000',
      fee: '0.01',
      amount: 0,
      confirmSendDialog: false,
      rawTx: 'loading...',
      canSend: false,
      sending: false
    }
  },
  computed: {
    params: function() {
      if (this.method === null) {
        return null
      }
      const inputs = this.parsedAbi[this.method].info.inputs
      if (inputs.length > 0) {
        return inputs
      }
      return null
    },
    payable: function() {
      if (this.method === null) {
        return null
      } else {
        return this.parsedAbi[this.method].info.payable
      }
    },
    notValid: function() {
      //@todo valid the address
      const gasPriceCheck = /^\d+\.?\d*$/.test(this.gasPrice) && this.gasPrice > 0
      const gasLimitCheck = /^\d+\.?\d*$/.test(this.gasLimit) && this.gasLimit > 0
      const feeCheck = /^\d+\.?\d*$/.test(this.fee) && this.fee > 0.0001
      return !(gasPriceCheck && gasLimitCheck && feeCheck && this.method !== null)
    }
  },
  watch: {
    method: function() {
      this.inputParams = []
    }
  },
  methods: {
    decodeAbi() { // this function is ommited ... not called by commented @input="decodeAbi" in page form - aperez 08Nov2022
      try {
        const abiJson = JSON.parse(this.abi) 
        this.parsedAbi = []
        for (let i = 0; i < abiJson.length; i++) {
          this.parsedAbi[i] = {text: abiJson[i]['name'], value: i, info: abiJson[i]}
        }
      } catch (e) {
        this.$root.log.error('send_to_contract_decode_abi_error', e.stack || e.toString() || e)
        return true
      }
    },
    async send() {
      try {
	      const encodedData = abi.encodeMethod(this.parsedAbi[this.method].info, this.inputParams).substr(2)
        this.confirmSendDialog = true
        try {
          this.rawTx = await webWallet.getWallet().generateSendToContractTx(this.contractAddress, encodedData, this.gasLimit, this.gasPrice, this.fee, this.amount)
        } catch (e) {
          this.$root.log.error('send_to_generate_tx_error', e.stack || e.toString() || e)
          alert(e.message || e)
          this.confirmSendDialog = false
          return false
        }
        this.canSend = true
      } catch (e) {
        this.$root.error('Params error')
	      alert(e.message || e)
        this.$root.log.error('send_to_contract_encode_abi_error', e.stack || e.toString() || e)
        this.confirmSendDialog = false
        return false
      }
    },

    async confirmSend() {
      this.sending = true
      try {
        const txId = await webWallet.getWallet().sendRawTx(this.rawTx)
        this.confirmSendDialog = false
        this.sending = false
        const txViewUrl = server.currentNode().getTxExplorerUrl(txId);
        this.$root.success(`Successful sent! You can follow the transaction on <a href="${txViewUrl}" target="_blank">${txViewUrl}</a>`, true, 0);
        this.$emit('send')
      } catch (e) {
        alert(e.message || e)
        this.$root.log.error('send_to_contract_post_raw_tx_error', e.response || e.stack || e.toString() || e)
        this.confirmSendDialog = false
      }
    }
  }
}
</script>



